<template lang="en">
    <section>
        <loader :is-visible="isLoading"></loader>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle ">
                            <h4 name="header" class="modal-title pt-1">
                                {{ $t('LiveView') }} - {{Item.username}}
                            </h4>
                            <button class="close" v-on:click="StopStreaming()"><em aria-hidden="true" class="fa fa-times"></em> </button>
                        </div>
                        <div class="container-fluid">
                            <div class="mx-auto mt-5" style="max-width:1280px;">
                                <div class="row">
                                    <div class="livescreen_leftbase">
                                        <div class="led_screen mb-3 float-left w-100">
                                            <img :src="imageSrc" alt="Base64 Image" v-if="imageSrc"/>
                                            <div class="fullscreen_btn" :title="$t('FullScreen')">
                                                <a href="javascript:;"  v-on:click="ShowFullScreen()"><em class="fa fa-expand"></em></a>
                                            </div>
                                        </div>
                                        <div class="row">
                                            <div class="col-lg-6 text-left font-23">07-05-2024 - 04:52 PM</div>
                                            <div v-if="isStartedRecording" class="col-lg-6 text-right font-23">[<em class="fa fa-circle text-danger"></em> rec] 00:02:04</div>
                                        </div>
                                    </div>
                                    <div class="livescreen_rightbase">
                                        <div class="cont_desc">
                                            <h3>{{$t('SystemName')}}:</h3>
                                            <span>Talygen-SYS-142</span>
                                        </div>

                                        <div class="cont_desc">
                                            <h3>{{$t('OperatingSystem')}}:</h3>
                                            <span>Window 10</span>
                                        </div>

                                        <div class="cont_desc">
                                            <h3>{{$t('IpAddress')}}:</h3>
                                            <span>192.168.0.210</span>
                                        </div>

                                        <div class="cont_desc">
                                            <h3>{{$t('MACAddressVdesk')}}:</h3>
                                            <span>00-BO-DO-63-C2-26</span>
                                        </div>

                                        <div class="cont_desc">
                                            <h3>{{$t('Shift')}}:</h3>
                                            <span>09:00AM - 06:00PM</span>
                                        </div>
                                        <div class="cont_desc">
                                            <h3>{{$t('Location')}}:</h3>
                                            <span>New York</span>
                                        </div>
                                        <div class="cont_btns col-lg-12">
                                            <div class="smallbtns text-center w-100 d-flex mb-2">
                                                <a class="btn btn-yellow" :title="$t('ForceLogout')" v-on:click="ForceLogout()"><em class="fa fa-keyboard-o"></em></a> 
                                                <a class="btn btn-blue" :title="isFreezeKeyboard ? $t('UnfreezeKeyboard')  : $t('FreezeKeyboard')" v-on:click="FreezeUnfreezeKeyboard()"><em class="fa fa-keyboard-o"></em></a> 
                                                <a class="btn btn-purple" :title="isFreezeMouse ? $t('UnfreezeMouse')  : $t('FreezeMouse')" v-on:click="FreezeUnfreezeMouse()"><em class="fa fa-keyboard-o"></em></a>
                                            </div>
                                            <div class="largebtns">
                                                <a class="btn btn-primary" v-if="!isStartedRecording" v-on:click="StartRecording()">{{$t('StartRecording')}}</a>
                                                <a class="btn btn-danger" v-if="isStartedRecording" v-on:click="StopRecording()">{{$t('StopRecording')}}</a>
                                                <a class="btn btn-success">{{$t('TakeScreenshot')}}</a>
                                                <a class="btn btn-secondary" v-if="isShowDownload" v-on:click="DownloadRecording()">{{$t('DownloadNow')}}</a>
                                                <a class="btn btn-danger">{{$t('ExitFullScreen')}}</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>


export default{
    props:{
        Item:{
            type:Object,
            required:true
        }
    },
    data(){
        return{
            isLoading:false,
            isStartedRecording:false,
            isShowDownload:false,
            isFreezeKeyboard:false,
            isFreezeMouse:false,
            isLogout:false,
            imageSrc: ''
        }
    },
    created(){
        var vm = this;
        vm.LiveStreaming();
    },
    sockets:{
        imageData(data){
            var vm = this;
            vm.imageSrc = `data:image/jpeg;base64,${data.imageBuffer}`;
        },
        stopImageTransfer(){
            var vm =this;
            vm.imageSrc='';
        }        
    },
    methods:{
        LiveStreaming(){
            var vm = this;
            vm.$socket.emit('startStreaming',vm.Item);
        },
        StopStreaming(){
            var vm =this;
            vm.$socket.emit('stopStreaming',vm.Item);
            vm.$emit('close');
        },
        StartRecording(){
            var vm = this;
            vm.isStartedRecording=true;
            vm.isShowDownload=false;
        },
        StopRecording(){
            var vm = this;
            vm.isStartedRecording=false;
            vm.isShowDownload=true;
        },
        DownloadRecording(){
            var vm = this;
        },
        ForceLogout(){
            var vm = this;
            vm.isLogout=true;
            vm.confirmR(vm.$t("AreYouSureYouWantToLogOffUserSystem"), true,'', function () {
                //vm.$socket.emit('forceLogout',vm.Item);               
            });
            

        },
        FreezeUnfreezeKeyboard(){
            var vm = this;
            //vm.isFreezeKeyboard = vm.isFreezeKeyboard ? false : true;
            if(vm.isFreezeKeyboard){
                vm.isFreezeKeyboard=false;
                vm.$socket.emit('unfreezeKeyboard',vm.Item)
            }
            else{
                vm.isFreezeKeyboard=true;
                vm.$socket.emit('freezeKeyboard',vm.Item)
            }
            
        },
        FreezeUnfreezeMouse(){
            var vm = this;
            //vm.isFreezeMouse = vm.isFreezeMouse ? false : true;
            if(vm.isFreezeMouse){
                vm.isFreezeMouse=false;
                vm.$socket.emit('unfreezeMouse',vm.Item)
            }
            else{
                vm.isFreezeMouse=true;
                vm.$socket.emit('freezeMouse',vm.Item)
            }
        },
        ShowFullScreen(){
            var vm = this;
        },
        // ShowFullScreen(elem) {
        //     elem = elem || document.documentElement;
        //     if (!document.fullscreenElement && !document.mozFullScreenElement &&
        //         !document.webkitFullscreenElement && !document.msFullscreenElement) {
        //         if (elem.requestFullscreen) {
        //             elem.requestFullscreen();
        //         } else if (elem.msRequestFullscreen) {
        //             elem.msRequestFullscreen();
        //         } else if (elem.mozRequestFullScreen) {
        //             elem.mozRequestFullScreen();
        //         } else if (elem.webkitRequestFullscreen) {
        //             elem.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
        //         }
        //     } else {
        //         if (document.exitFullscreen) {
        //             document.exitFullscreen();
        //         } else if (document.msExitFullscreen) {
        //             document.msExitFullscreen();
        //         } else if (document.mozCancelFullScreen) {
        //             document.mozCancelFullScreen();
        //         } else if (document.webkitExitFullscreen) {
        //             document.webkitExitFullscreen();
        //         }
        //     }
        // },
    }
}
</script>