<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="true" :showCheckBox="false" :listType="[ListType]" ModuleName="Timer"
            SubModuleCode="Timer" IdentityColumn="user_id" :HeaderText="$t('LiveMonitoring')" :ListData="UserList"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'user_status'">
                    <a class="inactive-disabled">
                            <span :class="{'btn d-inline-block text-center text-truncate btn-success':data.row.user_status=='Active',
                            'btn d-inline-block text-center text-truncate btn-danger' : data.row.status_name=='Stopped',
                            'btn d-inline-block text-center text-truncate btn-warning' : data.row.status_name=='Idle',
                            'btn d-inline-block text-center text-truncate btn-primary' : data.row.status_name=='Slacking'}">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'productive_type'">
                    <a class="inactive-disabled">
                        <span :class="{'btn d-inline-block text-center text-truncate btn-warning':data.row.productive_type=='Neutral',
                            'btn d-inline-block text-center text-truncate btn-success' : data.row.status_name=='Productive',
                            'btn d-inline-block text-center text-truncate btn-secondary' : data.row.status_name=='Unproductive'}">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'left_time'">
                    {{ data.row[data.column.COLUMN_NAME]=='00:00:00' ? '-' : data.row[data.column.COLUMN_NAME] }}
                </template>
            </template>
        </tg-list>
        <LiveView v-if="isShowLive" :Item="LiveItem" @close="isShowLive=false"></LiveView>
    </section>
</template>

<script>
import Schema from '../LiveMonitoring/Schema';
import DataService from '../../services/DataService';
import LiveView from '../../components/Modal/modalLiveView.vue';
export default {
    components:{
        LiveView
    },
    data(){
        return{
            Headers: [
                {
                    "COLUMN_NAME": 'username',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Employee_Name'),
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": 'user_status',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('CurrentStatus'),
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": 'department_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Department'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'arrival_time',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Arrivaltime'),
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": 'left_time',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Lefttime'),
                    "DISPLAY_ORDER": 5,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": 'application_display_name',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ActivityWorking'),
                    "DISPLAY_ORDER": 6
                },
                {
                    "COLUMN_NAME": 'productive_type',
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('AppProductivity'),
                    "DISPLAY_ORDER": 7,
                    settings: {
                        isInSlot: true,
                    },
                }

            ],
            widgets: [
                {
                    icon: "fa fa-list-ul",
                    value: 0,
                    name: "No. of employees",
                    class: "one_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-ticket",
                    value: 0,
                    name: "Currently Active",
                    class: "two_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-reply",
                    value: 0,
                    name: "Inactive",
                    class: "three_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-calendar-check-o",
                    value: 0,
                    name: "Idle",
                    class: "four_wid",
                    onClick: this.widgetClick
                },
                {
                    icon: "fa fa-file-text",
                    value: 0,
                    name: "Slacking",
                    class: "five_wid",
                    onClick: this.widgetClick
                }
            ],
            isLoading:false,
            ListType:'List',
            UserList:[],
            listheaderbuttons:[],
            TotalRecords:0,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray:[],
            sortBy: '',
            sortExp: '',
            PageNumber:1,
            searchCondition:'',
            isShowLive:false,
            intervalId:null
        }
    },
    beforeDestroy() {
        var vm = this;
        // Clear the interval when the component is destroyed
        vm.stopInterval();
    },
    async created (){
        var vm = this;
        await Schema.bindleftCommonSearchdropdown(this, "Timer", "USERS");
        await Schema.bindleftCommonSearchdropdown(this, "Timer", "DEPARTMENT");
        vm.startInterval();
    },
    methods:{
        async FetchData(){
            var vm =this;
            var userIds ='';
            var departmentIds ='';
            var statusIds ='';
            var shitfIds ='';
            vm.leftSearchFields.forEach(x=>{
                if(x.paramName=='userIds'){
                    userIds=x.value;
                }else if(x.paramName=='departmentIds'){
                    departmentIds=x.value;
                }else if(x.paramName=='statusIds'){
                    statusIds=x.value;
                }else if(x.paramName=='shitfIds'){
                    shitfIds=x.value;
                }
            })
            var isShiftView='NO';
            var params = `userIds=${userIds}&departmentIds=${departmentIds}&statusIds=${statusIds}&shitfIds=${shitfIds}&isShiftView=${isShiftView}`;
            await DataService.GetLiveUsers(params).then(response => {
                vm.UserList=[];
                if(response.data && response.data.length > 0){
                    vm.UserList=response.data;
                }
            })
        },
        widgetClick(){

        },
        actionButtonClick(callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case 'GotoLive':
                    vm.GotoLive(item);
                    break;
            }
        },
        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Live View':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            var vm =this;
            vm.pageSize = value.PageSize;
            vm.pageNum = value.PageNumber;
            vm.FetchData();
        },
        leftCommonSearch: function (value) {
            var vm =this;
            vm.PageNumber = value.PageNumber;
            vm.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                vm.searchCondition = value.searchConditionJson;
            } else {
                vm.searchCondition = "";
            }
            vm.FetchData();
        },
        commonHeaderButtonClick(event) {
        },
        sortdata: function (s) {
            var vm =this;
            var SortDir = "sort tb_headerSortDown";
            vm.sortBy = s;
            if (s === vm.sortBy) {
                if (vm.sortExp == "") {
                    vm.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (vm.sortExp == "ASC") {
                    vm.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    vm.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            vm.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        GotoLive(item){
            var vm =this;
            vm.LiveItem=item;
            vm.isShowLive=true;
        },
        startInterval() {
            var vm = this;
            vm.FetchData();
            // Set up the interval to call the method every 2 minutes (120000 milliseconds)
            vm.intervalId = setInterval(vm.FetchData, 120000);
        },
        stopInterval() {
            var vm = this;
            // Clear the interval when you want to stop it
            clearInterval(vm.intervalId);
        }
    }
}
</script>